const newsApiService = new NewsApiService();

refs.form.addEventListener('submit', onSearchServ); // ÑÑÐ°Ð²Ð»Ñ Ð¿ÑÐ¾ÑÐ»ÑÑÐ¾Ð²ÑÐ²Ð°ÑÐ° Ð½Ð° ÐºÐ½Ð¾Ð¿ÐºÑ Ñ Ð¾ÑÑÐ¸Ð¼ÑÑ Ð´Ð°Ð½Ð½Ñ Ð· ÑÐµÑÐ²ÐµÑÐ°
refs.moreBtn.addEventListener('click', onLoadMoreBtn); // ÑÑÐ°Ð²Ð»Ñ Ð¿ÑÐ¾ÑÐ»ÑÑÐ¾Ð²ÑÐ²Ð°ÑÐ° Ð½Ð° ÐºÐ½Ð¾Ð¿ÐºÑ Ð´Ð»Ñ Ð·Ð°Ð³ÑÑÐ·ÐºÐ¸ ÐºÐ¾Ð½ÑÐµÐ½ÑÑ (Ð±ÑÐ»ÑÑÐµ)

function onSearchServ(evt) { // ÑÑÐ½-Ñ Ð²ÑÐ´Ð¿ÑÐ°Ð²ÐºÐ¸ Ð·Ð°Ð¿ÑÐ¾ÑÑ Ñ Ð¾ÑÑÐ¸Ð¼Ð°Ð½Ð½Ñ ÑÐµÐ·ÑÐ»ÑÑÐ°ÑÑ Ð· ÑÐµÑÐ²ÐµÑÐ° 
    evt.preventDefault();
    
    newsApiService.query = evt.currentTarget.elements.searchQuery.value;

    if (newsApiService.query.trim() === '') {
        return alert('Ð²Ð²ÐµÐ´Ð¸ ÑÑÐ¾-ÑÐ¾')
    }
    
    refs.moreBtn.style.display = 'block';
    newsApiService.resetPage();
    clearAppendArticle();
    onLoadMoreBtn();
}

function onLoadMoreBtn() { // ÑÑÐ½-Ñ Ð´Ð»Ñ Ð¿ÑÐ´Ð³ÑÑÐ·ÐºÐ¸ ÑÑÐ¾ÑÑÐ½Ð¾Ðº
    refs.moreBtn.setAttribute('disabled', 'disabled');
    newsApiService.fetchArticles().then(appArticle => {
        appendArticle(appArticle);
        refs.moreBtn.removeAttribute('disabled');
    });
}

function appendArticle(hits) { // ÑÐ¾ÑÐ¼ÑÑÐ¼Ð¾ ÐºÐ°ÑÑÐºÑ ÑÐ° Ð·Ð°Ð¿Ð¾Ð²Ð½ÑÑÐ¼Ð¾ ÐºÐ¾Ð½ÑÐµÐ½ÑÐ¾Ð¼
  const photoCards = hits.map(({ webformatURL, largeImageURL, tags, likes, views, comments, downloads }) => {
    return `
      <div class="photo-card">
        <a class="photo-lightbox" href="${largeImageURL}">
          <img class="photo-card-img" src="${webformatURL}" alt="${tags}" loading="lazy" />
        </a>
        <div class="info">
          <h3 class="txt-title">Tag: ${tags}</h3>
          <p class="info-item"><b>Likes: ${likes}</b></p>
          <p class="info-item"><b>Views: ${views}</b></p>
          <p class="info-item"><b>Comments: ${comments}</b></p>
          <p class="info-item"><b>Downloads: ${downloads}</b></p>
        </div>
      </div>
    `;
  }).join('');
  
  refs.gallery.insertAdjacentHTML('beforeend', photoCards);
  
    const lightBoxImg = new SimpleLightbox('.gallery a', {
        captionsData: 'alt',
        captionsDelay: 300,
    });
}

function clearAppendArticle() { // ÑÑÐ½-Ñ Ð´Ð»Ñ Ð¾ÑÐ¸ÑÐµÐ½Ð½Ñ Ð³Ð°Ð»ÐµÑÐµÑ Ð¿ÑÐ¸ Ð½Ð¾Ð²Ð¾Ð¼Ñ Ð·Ð°Ð¿Ð¸ÑÑ
    refs.gallery.innerHTML = '';
}